<template>

  <div class="ui middle aligned center aligned grid">
    <div class="column">
      <h2 class="ui teal image header">
        <div class="content">
          
        </div>
      </h2>
      <map :location.sync='user.location'></map>
      <form class="ui large form">
        <div class="ui stacked segment">
          <div class="field">
            <div class="ui left icon input">
             <ui-label></ui-label>
              <input type="text" name="email" v-model="user.email" placeholder="alex@139.com">
            </div>
          </div>
          <div class="field">
            <div class="ui left icon input">
              <ui-label></ui-label>
              <input type="password" name="password" v-model="user.password" placeholder="6~10">
            </div>
          </div>
          
          <div class="field">
            <div class="ui left icon input">
              <ui-label></ui-label>
              <input type="text" name="name" v-model="user.name" placeholder="">
            </div>
          </div>

          <div class="field">
            <div class="ui left icon input">
              <ui-label></ui-label>
              <input type="text" name="mobile" v-model="user.mobile" placeholder="">
            </div>
          </div>
  
          <div class="ui fluid large teal submit button">
            <ui-button css="basic" @click='register(user)'>
              <ui-icon css="user"></icon>
            </ui-button>
          </div>
        </div>

        <div class="ui error message"></div>
        <div class="ui message">
          ? <a class="item" v-link="{ name: 'login'}"></a>
        </div>
      </form>
    </div>
  </div>

  </body>

</template>
<script>

import { register } from '../vuex/actions'
import Map from '../components/Map' 
export default {
   components:{Map},
   data: ()=>{
      return {user:{name:'',email:'',password:'',mobile:'',
        location:{longitude:113.28, latitude:23.09}
      }}
   }, 
   vuex: {
     actions: {
      register
    }
   }
  		
}


